@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  display: flex;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  width: 100%;
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
}

.favorite-players {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 20px;
}

.favorite-player {
  display: flex;
  flex-flow: row wrap;
  padding: 5px 10px;
  align-items: center;
  background-color: var(--bg-item);
  border-bottom: 1px solid var(--br-color-favorite-item);
  position: relative;
  transition: 0.5s;
}

.favorite-player:hover {
  background-color: var(--bg-item-hover);
  transition: 0.5s;
}

.player__club-wrap {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #fff;
}

.player__club {
  width: 50%;
}

.player-info {
  margin-left: 15px;
}

.player-info__text {
  margin: 0;
}

.unfollow-player {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
}

.favorite-teams {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 20px;
}

.favorite-team {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  background-color: var(--bg-item);
  border-bottom: 1px solid var(--br-color-favorite-item);
  position: relative;
  transition: 0.5s;
}

.favorite-team:hover {
  background-color: var(--bg-item-hover);
  transition: 0.5s;
}

.favorite-team__img-wrap {
  width: 45px;
  height: 45px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.favorite-team__img {
  width: 50%;
}

.favorite-team__name {
  margin-left: 15px;
}

.unfollow-team {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
}

.following-players {
  margin-top: 20px;
}

.profile-header {
  height: 235px;
  background-color: var(--bg-profile-header);
  position: relative;
}

.profile-avatar {
  display: flex;
  width: 300px;
  height: 300px;
  overflow: hidden;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}

.profile__name {
  margin-top: 160px;
  font-size: 48px;
  text-align: center;
}

.profile__text {
  text-align: center;
}

.following__title {
  margin-left: 15px;
}
.standings__head {
  background-color: var(--bg-table-header);
}

.standings__head-cell {
  padding: 5px;
}

.standings__row {
  border-bottom: 1px solid #efefef;
}

.mobile-cell {
  display: none;
}

@media (max-width: 880px) {
  .standings__head-cell:last-child {
    display: none;
  }
}

@media (max-width: 700px) {
  .standings__head-cell:nth-child(10),
  .standings__head-cell:nth-child(9),
  .standings__head-cell:nth-child(8) {
    display: none;
  }
}

@media (max-width: 500px) {
  .desctop-cell {
    display: none;
  }
  .mobile-cell {
    display: block;
  }
  .standings__head-cell {
    padding: 3px;
  }
}
.team-form-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.team-form-list__item {
  width: 25px;
  height: 25px;
  font-size: 12px;
  border-radius: 7px;
  color: white;
  margin: 0 2px;
  line-height: 25px;
}

.team-form-list__item.W {
  background-color: yellowgreen;
}

.team-form-list__item.L {
  background-color: red;
}

.team-form-list__item.D {
  background-color: gray;
}

.standings {
  width: 100%;
  max-width: 1280px;
  border-collapse: collapse;
  text-align: center;
  border: 1px solid #efefef;
}

.standings__head-cell {
  padding: 5px;
}

.standings__row {
  border-bottom: 1px solid #efefef;
}

.mobile-cell {
  display: none;
}

.football-team {
  display: flex;
  text-align: left;
  align-items: center;
  text-decoration: none;
  color: inherit;
}

.football-team:hover {
  cursor: pointer;
  text-decoration: underline;
}

.football-team__logo {
  width: 30px;
}

.football-team__name {
  padding-left: 15px;
}

@media (max-width: 880px) {
  .standings__cell:last-child {
    display: none;
  }
}

@media (max-width: 700px) {
  .standings__cell:nth-child(10),
  .standings__cell:nth-child(9),
  .standings__cell:nth-child(8) {
    display: none;
  }
}

@media (max-width: 500px) {
  .desctop-cell {
    display: none;
  }
  .mobile-cell {
    display: block;
  }
}

.standings {
  width: 100%;
  max-width: 1280px;
  border-collapse: collapse;
  text-align: center;
  border: 1px solid var(--br-color-table);
}

@media (max-width: 700px) {
  .standings {
    font-size: 13px;
  }
}

@media (max-width: 500px) {
  .standings {
    font-size: 11px;
  }
}

.preloader {
  width: 60px;
  height: 60px;
  position: fixed;
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
}

.preloader:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  border: 6px solid transparent;
  border-bottom-color: #1976d2;
  animation: preload 1.2s linear infinite;
}

@keyframes preload {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.tournament-list {
  padding: 0;
}

.tournament__title {
  margin-left: 20px;
}

.tournament {
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 10px 15px;
  transition: 0.5s;
  color: inherit;
}

.tournament:hover {
  background: var(--bg-item-hover);
  transition: 0.5s;
}

.tournament:active {
  background-color: #ffc400;
}

.tournament-img__wrap {
  width: 65px;
}

.tournament__img {
  width: 100%;
  display: block;
}

.tournament__name {
  font-size: 24px;
  margin-left: 20px;
}

.follow {
  display: block;
  border: none;
  background-color: transparent;
  padding: 5px 10px;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.follow::before ,
.follow::after {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 100%;
  background-color: #2962ff;
  border-radius: 4px;
  transition: 0.3s;
  z-index: -1;
}

.follow:focus {
  outline: none;
}

.follow:hover::before {
  right: 0%;
}

.follow::after {
  right: 0;
  background-color: #0091ea;
  z-index: -2;
}

.follow:active::before  {
  background-color: #0d47a1;
}

.unfollow {
  display: block;
  border: none;
  background-color: transparent;
  padding: 5px 10px;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.unfollow::before,
.unfollow::after {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 100%;
  background-color: #9e9e9e;
  border-radius: 4px;
  transition: 0.3s;
  z-index: -1;
}

.unfollow:focus {
  outline: none;
}

.unfollow:hover::before {
  right: 0%;
}

.unfollow::after {
  right: 0;
  background-color: #78909c;
  z-index: -2;
}

.unfollow:active::before  {
  background-color: #607d8b;
}

.card {
  font-size: 14px;
  cursor: pointer;
  position: relative;
  width: 200px;
  height: 245px;
  margin: 10px 5px;
  perspective: 1000px;
}

.card__front,
.card__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 10px 15px;
  transition: transform 0.5s;
  box-sizing: border-box;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  box-shadow: 2px 2px 2px 0px var(--card-shadow-calor);
  border-radius: 8px;
}

.card__front {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: var(--bg-player-fr-card);
}

.card__back {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: var(--bg-player-bc-card);
  transform: rotateY(180deg);
}

.card:hover .card__front {
  transform: rotateY(180deg);
  transition: transform 0.5s;
}

.card:hover .card__back {
  transform: rotateY(360deg);
  transition: transform 0.5s;
}

.card__text {
  margin: 0;
}

.club-info {
  display: flex;
  flex-wrap: wrap;
  background-color: var(--bg-team-header);
  padding: 20px;
}

.club-info__img-wrap {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
}

.club-info__img {
  display: block;
  margin: auto;
  width: 65%;
}

.club-info__col {
  margin-left: 25px;
}

.club-info__text,
.team__title {
  font-size: 20px;
  font-weight: 500;
}

.team {
  width: 100%;
  padding: 0px 10px;
  box-sizing: border-box;
}

.team-block {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
}

@media (max-width: 800px) {
  .club-info {
    flex-direction: column;
    align-items: center;
  }
  .club-info__col {
    margin: 0;
  }
}

.nav-links {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  overflow-y: auto;
}

.nav-links__item {
  display: block;
  padding: 10px 20px;
  width: 100%;
  box-sizing: border-box;
  text-decoration: none;
  color: inherit;
  transition: 0.5s;
}

.nav-links__item:hover {
  background-color: var(--bg-item-hover);
  transition: 0.5s;
}

.nav-links__name {
  margin: 0;
  font-size: 24px;
}

.active {
  background-color: #ffc400;
}

.theme-swithcer {
  width: 60px;
  height: 30px;
  background: var(--bg-switcher);
  border-radius: 15px;
  margin: 10px;
  cursor: pointer;
  position: relative;
}

.theme-swithcer__ball {
  width: 26px;
  height: 26px;
  background: var(--bg-switcher-ball);
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 32px;
  z-index: 2;
  transition: 0.5s;
}

.theme-swithcer__moon,
.theme-swithcer__sun {
  width: 25px;
  position: absolute;
  top: 3px;
  left: 3px;
  pointer-events: none;
}

.theme-swithcer__moon {
  left: 31px;
}

.theme-swithcer__outer {
  margin-left: auto;
  margin-right: 30px;
}

.dark .theme-swithcer__ball {
  left: 2px;
  transition: 0.5s;
}

.navigation {
  position: fixed;
  height: 50px;
  width: 100%;
  top: 0;
  left: 0;
  background-color: var(--bg-nav);
  border-bottom: 1px solid #263238;
  margin: 0;
  padding: 0;
  z-index: 3;
}

.menu-icon {
  display: block;
  width: 32px;
  height: 32px;
  background-image: url(/football-project-react/static/media/sprite.4c1a05ad.png);
  background-position: 2px 0;
  cursor: pointer;
  top: 10px;
  left: 25px;
  position: absolute;
  z-index: 4;
  transition: left 0.3s;
}

.dark .menu-icon {
  background-position: 80px 0;
}

.menu-icon.open {
  left: 280px;
  background-position: 32px 0;
  transition: left 0.3s;
}

.overlay {
  display: none;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  opacity: 0.7;
  position: fixed;
  top: 0;
}

.overlay.open {
  display: block;
  z-index: 3;
}

.menu-wrap {
  position: absolute;
  height: 100vh;
  width: 275px;
  top: 0;
  left: -275px;
  overflow: hidden;
  background-color: var(--bg-menu);
  z-index: 4;
}

.menu-wrap.open {
  left: 0;
  transition: 0.3s;
}

.main-content {
  width: 100%;
  max-width: 1280px;
  margin: 50px auto 20px;
}
* {
  font-family: "Langar";
}

.main-container.light {
  --bg-nav: #fbfbfb;
  --bg-page: #fff;
  --bg-menu: #fbfbfb;
  --color-text: #000;
  --bg-menu-hover: #eeeeee;
  --bg-item: #eeeeee;
  --bg-item-hover: #cfd8dc;
  --br-color-favorite-item: #26c6da;
  --bg-profile-header: #1a237e;
  --bg-switcher: #000;
  --bg-switcher-ball: #fff;
  --br-color-table: #efefef;
  --bg-table-header: #f1f1f1;
  --bg-team-header: #f50057;
  --bg-player-fr-card: #eeeeee;
  --bg-player-bc-card: #64b5f6;
  --card-shadow-calor: #eeeeee;
  transition: 0.5s;
}

.main-container.dark {
  --bg-nav: #161b22;
  --bg-page: #06090f;
  --bg-menu: #161b22;
  --color-text: #fff;
  --bg-item-hover: #37474f;
  --bg-item: #546e7a;
  --br-color-favorite-item: #161b22;;
  --bg-profile-header: #546e7a;
  --bg-switcher: #fff;
  --bg-switcher-ball: #000;
  --br-color-table: #161b22;
  --bg-table-header: #78909C;
  --bg-team-header: #130a3e;
  --bg-player-fr-card:#607D8B;
  --bg-player-bc-card:#37474F;
  --card-shadow-calor: #009688;
  transition: 0.5s;
}

.main-container {
  width: 100%;
  background-color: var(--bg-page);
  color: var(--color-text);
  display: flex;
  flex: 1 0 auto;
}

