.team-form-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.team-form-list__item {
  width: 25px;
  height: 25px;
  font-size: 12px;
  border-radius: 7px;
  color: white;
  margin: 0 2px;
  line-height: 25px;
}

.team-form-list__item.W {
  background-color: yellowgreen;
}

.team-form-list__item.L {
  background-color: red;
}

.team-form-list__item.D {
  background-color: gray;
}
