.standings {
  width: 100%;
  max-width: 1280px;
  border-collapse: collapse;
  text-align: center;
  border: 1px solid var(--br-color-table);
}

@media (max-width: 700px) {
  .standings {
    font-size: 13px;
  }
}

@media (max-width: 500px) {
  .standings {
    font-size: 11px;
  }
}
